export default {
	data() {
		return {
			isShopOwner: false,
			userInfo: {
				loginStatus: false,
			}
		}
	},
	computed: {
		phone() {
			let phone = this.userInfo.phone
			console.log('phone 1:', phone.substring(0, 3))
			console.log('phone 2:', phone.substring(3, 7))
			console.log('phone 3:', phone.substring(7))
			return phone.substring(0, 3) + '****' + phone.substring(7);
		}
	},
	created() {
		this.updateUserInfo()
		
	},
	methods: {
		goToLogin() {
			this.$login.openLoginPage()
		},
		updateUserInfo() {
			let userInfo = this.$userInfo.getUserInfo()
			if (userInfo) {
				this.userInfo = userInfo
			} else {
				this.userInfo = {
					loginStatus: false,
					phone: '',
					username: ''
				}
			}
			// 判断是否为店长
			let roles = this.userInfo.roles;
			this.isShopOwner = false;
			if (roles) {
				for (let i = 0; i < roles.length; i ++) {
					let role = roles[i];
					if (role.name == 'ROLE_SHOPOWNER') {
						this.isShopOwner = true;
					}
				}
			}
		},
		openMasterManage() {
			uni.navigateTo({
				url: '/pages/my/master-manage/master-manage'
			})
		},
		confirmLogout() {
			let that = this
			uni.showModal({
				title: '退出登录',
				content: '是否确定要退出登录？',
				success(res) {
					if (res.confirm) {
						that.logout()
					}
				}
			})
		},
		logout() {
			let that = this
			let userInfo = uni.getStorageSync('LOGIN_USER_INFO')
			that.$api.apiGet({
				url: '/logout'
			})
			.then(() => {
				// if (res.statusCode == 200) {
					// uni.removeStorageSync('LOGIN_USER_INFO')
				that.$userInfo.removeUserInfo()
				that.$emit('logout')
				that.updateUserInfo()
				uni.showToast({
					title: '退出登录成功'
				})
				// }
			})
			.catch(() => {
				uni.showToast({
					title: '退出登录失败'
				})
			})
			
		}
	}
}
